import { merge } from '../helper';

merge(
    /^\/karma\.php/,
    undefined,
    {
        '\n\tYou are about to imbue ': ' ',
        ' with ': ' ',
        ' karma.': ' karma',
        '\n\tIf you wish, you can leave a short message below.\n': '\n\t\n',
        Imbue: '',

        '\nYou cannot imbue yourself with Karma!\n': ' Karma',

        'Please wait longer between karma imbue attempts.': ' Karma ',

        'You have imbued ': ' ',
        'Your Karma Influence Power has been drained, and is now ': ' Karma  ',

        '\nYou have imbued this member with karma too recently.': ' Karma',
        'Time remaining before you can imbue this member again:': '',
    },
    [
        [/^ with ([\d.]+) points? of karma.$/, ' $1  Karma'],
        [/^([\d.]+) days and ([\d.]+) hours$/, '$1  $2 '],
        [/^([\d.]+) hours and ([\d.]+) minutes$/, '$1  $2 '],
        [/^([\d.]+) days$/, '$1 '],
        [/^([\d.]+) hours$/, '$1 '],
        [/^([\d.]+) minutes$/, '$1 '],
    ],
);
